import React, { useCallback, useEffect, useState } from "react";
import { useGlobalContext } from "../../Context";
import { signinInputFields } from "../../Variables/signinInputFileds";
import InputFiled from "./InputField";
import Oauth from "./Oauth";

const SignIn = () => {
  //Variables
  const { user, feedback, check, setCheck, setFeedback, setShowFeedback } =
    useGlobalContext();
  const [look, setLook] = useState(false);

  //Functions

  const handleSubmit = async (e) => {
    e.preventDefault();
    setCheck(true);
  };

  const checkFields = useCallback(() => {
    const answer = feedback.find((item) => item.type === "invalid-fb");
    if (answer === undefined) {
      return true;
    }
    return false;
  }, [feedback]);

  const checkEmail = useCallback(() => {
    const emailRegex = /.+@.+\..+/;
    if (!emailRegex.test(user.email)) {
      setFeedback((currentFB) => {
        return [
          ...currentFB,
          {
            field: "email",
            type: "invalid-fb",
            message: "Invalid email structure",
          },
        ];
      });
      return false;
    }
    return true;
  }, [setFeedback, user.email]);

  const checkPassword = useCallback(() => {
    const passwordRegex = /.{8,}/;
    if (!passwordRegex.test(user.password)) {
      setFeedback((currentFB) => {
        return [
          ...currentFB,
          {
            field: "password",
            type: "invalid-fb",
            message: "The password field should have at least 8 characters",
          },
        ];
      });
      return false;
    }
    return true;
  }, [setFeedback, user.password]);

  //UseEffects

  //When the submit button was pressed
  useEffect(() => {
    if (check) {
      setLook(true);
    }
  }, [check]);

  //To have the user fields complete and for make the verification of the fields

  useEffect(() => {
    if (look) {
      setFeedback([]);
      Object.entries(user).forEach((key) => {
        if (!key[1]) {
          setShowFeedback(true);
          setFeedback((currentFB) => {
            return [
              ...currentFB,
              {
                field: key[0],
                type: "invalid-fb",
                message: `The ${key} field can't be empty`,
              },
            ];
          });
        } else {
          if (key[0] === "email" && !checkEmail()) {
            return;
          }
          if (key[0] === "password" && !checkPassword()) {
            return;
          }
          setFeedback((currentFB) => {
            return [
              ...currentFB,
              {
                field: key[0],
                type: "valid-fb",
                message: `Looks good`,
              },
            ];
          });
        }
      });
      setCheck(false);
      setLook(false);
    }
  }, [
    checkEmail,
    checkPassword,
    look,
    setCheck,
    setFeedback,
    setShowFeedback,
    user,
  ]);

  //TO send the data to  the server

  //To check the fields and send the data

  useEffect(() => {
    if (checkFields()) {
      fetch("/server/register", {
        method: "POST", // or 'PUT'
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(user),
      })
        .then((response) => response.json())
        .then((data) => {
          if (data.message === "success") {
            window.location.href = "/";
          } else {
            if (data.error.name) {
              const newFeedback = feedback.map((item) => {
                if (item.field === "username") {
                  return {
                    ...item,
                    type: "invalid-fb",
                    message: "This username already exist",
                  };
                }
                return item;
              });
              setFeedback(newFeedback);
            } else {
              const newFeedback = feedback.map((item) => {
                if (item.field === "email") {
                  return {
                    ...item,
                    type: "invalid-fb",
                    message: "This email is already register",
                  };
                }
                return item;
              });
              setFeedback(newFeedback);
            }
          }
        });
    }
  }, [checkFields, feedback, setFeedback, user]);

  return (
    <div className="auth container-fluid min-vh-100">
      <div className="auth-container ">
        <h1 className=" text-primary  mb-4 fw-bold">Register</h1>
        <form className="needs-validation">
          {signinInputFields.map((item) => (
            <InputFiled name={item.name} type={item.type} key={item.id} />
          ))}
          <button className="btn btn-primary" onClick={handleSubmit}>
            Submit
          </button>
        </form>
      </div>
      <Oauth type={"Sign In"} />
    </div>
  );
};

export default SignIn;
