import React from "react";
import { BsTrashFill } from "react-icons/bs";

const ListItem = ({ _id, text, checked, removeItem, checkItem }) => {
  return (
    <div className="item  bg-primary">
      <input
        type="checkbox"
        checked={checked}
        onChange={() => checkItem(_id)}
      />
      <p className={`p ${checked && "p-checked"}`}>{text}</p>
      <i onClick={() => removeItem(_id)}>
        <BsTrashFill />
      </i>
    </div>
  );
};

export default ListItem;
