import React, { useCallback, useEffect, useState } from "react";
import { useGlobalContext } from "../../Context";
import ListItem from "./ListItem";

const List = ({ userID }) => {
  const { list, setList } = useGlobalContext();
  const [input, setInput] = useState("");
  const [username, setUsername] = useState("");

  const logout = () => {
    fetch("/server/logout");
    window.location.reload();
  };

  const getList = useCallback(() => {
    fetch("/server/getList", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ userID }),
    })
      .then((response) => response.json())
      .then((data) => {
        setUsername(data.username);
        setList(data.list);
      });
  }, [setList, userID]);

  const addItem = () => {
    if (input) {
      fetch("/server/add", {
        method: "POST", // or 'PUT'
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ input, userID }),
      })
        .then((response) => response.json())
        .then((data) => setList(data));
    }
    setInput("");
  };

  const removeItem = (id) => {
    fetch("/server/delete", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ userID, itemID: id }),
    })
      .then((response) => response.json())
      .then(() =>
        setList((currentList) => currentList.filter((item) => item._id !== id))
      );
  };

  const checkItem = (id) => {
    fetch("/server/check", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ userID, itemID: id }),
    })
      .then((response) => response.json())
      .then((data) => setList(data));
  };

  useEffect(() => {
    getList();
  }, [getList]);

  return (
    <div className="list bg-primary container-fluid min-vh-100 d-flex flex-column align-items-center justify-content-center">
      <h1 className="text-light">Welcome {username}</h1>
      <div className="list-container bg-light my-5">
        <div className="add-container d-flex gap-3 ">
          <div className="form-floating">
            <input
              id="add"
              type="text"
              className="form-control"
              placeholder="something"
              value={input}
              onChange={(e) => setInput(e.target.value)}
            />
            <label htmlFor="add">Type Someghing</label>
          </div>

          <button className="btn btn-primary btn-lg" onClick={addItem}>
            Add
          </button>
        </div>
        <div className="todos my-4">
          {list.length > 0 ? (
            list.map((item, index) => (
              <ListItem
                {...item}
                removeItem={removeItem}
                checkItem={checkItem}
                key={index}
              />
            ))
          ) : (
            <p>Looks like you have nothing to do</p>
          )}
        </div>
      </div>
      <button className="btn btn-dark" onClick={logout}>
        Logout
      </button>
    </div>
  );
};

export default List;
