import React from "react";
import { Link } from "react-router-dom";

const Home = () => {
  return (
    <div className="container-fluid min-vh-100  d-flex flex-column justify-content-center r">
      <h1 className="text-center mb-3">Welcome, Please Sign In</h1>

      <div className="container d-flex justify-content-center mt-3">
        <Link className="btn btn-dark me-3" to="/signin">
          Sign In
        </Link>
        <Link className="btn btn-outline-dark ms-3" to="/login">
          Log In
        </Link>
      </div>
    </div>
  );
};

export default Home;
