import React from "react";
import { BsGoogle } from "react-icons/bs";

const Oauth = ({ type }) => {
  return (
    <div className="oauth-container">
      <div className="oauth google-oauth bg-success">
        <i>
          <BsGoogle />
        </i>
        <a href="/auth/google/">{type} with Google</a>
      </div>
      {/* <div className="oauth google-oauth bg-primary">
        <i>
          <BsFacebook />
        </i>
        <a href="http://localhost:5000/auth/facebook">{type} with Facebook</a>
      </div> */}
    </div>
  );
};

export default Oauth;
