import React, { useState } from "react";
import Oauth from "./Oauth";

const LogIn = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState(false);

  const handleSubmit = (e) => {
    e.preventDefault();
    fetch("/server/login", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ username: email, password }),
    })
      .then((response) => {
        if (response.status >= 400) {
          setError(true);
          setPassword("");
        }
        return response.json();
      })
      .then((data) => {
        console.log(data);
        if (data.message === "success") {
          window.location.href = "/";
        }
      });
  };

  return (
    <div className="auth container-fluid  min-vh-100 d-flex flex-column align-items-center justify-content-center">
      <div className="auth-container ">
        <h1 className=" text-primary  mb-4 fw-bold">Log In</h1>
        {error && (
          <div className="lg-feedback">
            The Username or the password is not valid
          </div>
        )}

        <form className="needs-validation">
          <div
            className={`form-floating ${
              error && "invalid-fb"
            } mb-3 has-validation`}
          >
            <input
              type="text"
              className="form-control"
              id="floatingInput"
              placeholder="name@example.com"
              name="username"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
            <label htmlFor="floatingInput">Uername</label>
          </div>

          <div
            className={`form-floating ${
              error && "invalid-fb"
            } mb-3 has-validation`}
          >
            <input
              type="password"
              className="form-control"
              id="floatingPassword"
              placeholder="Password"
              name="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />
            <label htmlFor="floatingPassword ">Password</label>
          </div>
          <button className="btn btn-primary" onClick={handleSubmit}>
            Submit
          </button>
        </form>
      </div>
      <Oauth type={"Log In"} />
    </div>
  );
};

export default LogIn;
