import React, { useEffect, useState } from "react";
import { useGlobalContext } from "../../Context";

const InputField = ({ name, error, type }) => {
  const [input, setInput] = useState("");
  const [feedbackType, setFeedbackType] = useState("");
  const [fbMessage, setFbMessage] = useState("");
  const { setUser, check, feedback } = useGlobalContext();

  useEffect(() => {
    if (check) {
      setUser((currentUser) => {
        return { ...currentUser, [name]: input };
      });
    }
  }, [check, input, name, setUser]);

  useEffect(() => {
    feedback.forEach((element) => {
      if (element.field === name) {
        setFeedbackType(element.type);
        setFbMessage(element.message);
      }
    });
  }, [feedback, name]);

  return (
    <div className={`form-floating ${feedbackType} mb-3 has-validation `}>
      <input
        type={type}
        className="form-control"
        id={name}
        placeholder="something"
        name={name}
        value={input}
        onChange={(e) => setInput(e.target.value)}
      />
      <div className="feedback">{fbMessage}</div>
      <label htmlFor={name}>{name}</label>
    </div>
  );
};

export default InputField;
